import 'pixi-spine' // Do this once at the very start of your code. This registers the loader!
import * as PIXI from 'pixi.js';
import {Spine} from 'pixi-spine';

export default class Loader{
    private app:PIXI.Application
    constructor(){
        this.app = new PIXI.Application({ width: 1920, height: 1080, antialias: false});
        document.body.appendChild(this.app.view as any);
        this.init()
    }

    private init(){
        PIXI.Assets.load("assets/raptor/raptor-pro.json").then((resource) => {
            const animation = new Spine(resource.spineData);
            this.app.stage.addChild(animation);
        
            // add the animation to the scene and render...
            this.app.stage.addChild(animation);
            
            if (animation.state.hasAnimation('gun-grab')) {
                // run forever, little boy!
                animation.state.setAnimation(0, 'gun-grab', true);
                // dont run too fast
                animation.state.timeScale = 0.1;
                // update yourself
                animation.autoUpdate = true;
            }
        });
    }
}